import React, {useCallback, useEffect, useState} from "react";
import {InnerStyle} from "@/widget/base/wap_style";
import styled from "styled-components";
import {CartBottomWidget} from "@/view/cart/widget/cart_bottom";
import {ShoppingItemType, ShoppingMsgWidget} from "@/view/cart/widget/shopping_msg";
import {ShopItemType} from "@/config";

import choose02 from "@/assets/images/cart/choose02.png"
import unChoose02 from "@/assets/images/cart/choose02 (1).png"
import syicon01 from "@/assets/images/home/syicon01.png";
import AddImg from "@/assets/images/cart/add.png"
import MinusImg from "@/assets/images/cart/minus.png"
import {useParams} from "react-router-dom";
import {MokelistData} from "@/moke_data";


export const CartBody: React.FC = () => {

    const [listData, setList] = useState<{ data: ShoppingItemType[], i: number }>({data: MokelistData, i: 0})
    const {classification} = useParams();
    const MinusFn = useCallback((i) => {
        listData.data[i].quantity -= 1;
        setList({...listData, i: Math.random()})
    }, [listData])

    const AddFn = useCallback((i) => {
        listData.data[i].quantity += 1;
        setList({...listData, i: Math.random()})
    }, [listData])

    useEffect(() => {
        // setList({data: [], i: 0})

    }, [classification])

    return <WapStyle>
        <div className={'list'}>
            {
                listData.data.map((ev, i) => {
                    return <ItemWidget key={i + "cart" + classification}
                                       msg={{...ev, shopType: classification}}
                                       setAc={() => {
                                           listData.data[i].isAc = !listData.data[i].isAc;
                                           setList({...listData, i: Math.random()})
                                       }}
                                       widget={
                                           <AddWidget add={() => AddFn(i)} minus={() => MinusFn(i)}
                                                      quantity={ev.quantity}/>
                                       }/>;
                })
            }
        </div>
        <div className="footer">
            <CartBottomWidget payData={listData}/>
        </div>
    </WapStyle>
}

const AddWidget: React.FC<{ add: Function, minus: Function, quantity: number }> = ({add, minus, quantity}) => {
    return (<AddStyle>
        <div>
            <span onClick={() => {
                if (quantity > 1) {
                    minus()
                }
            }}><img src={MinusImg} alt=""/></span>
            <p>{quantity}</p>
            <span onClick={() => {
                add()
            }}><img src={AddImg} alt=""/></span>
        </div>

    </AddStyle>)
}

const AddStyle = styled.div`
  display: flex;
  justify-content: end;

  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      width: 14px;
      height: 14px;
      display: block;
    }

    p {
      margin: 0 10px;
      width: 30px;
      height: 17px;
      text-align: center;
      line-height: 17px;
      background: rgba(237, 237, 237, 0.39);
      border-radius: 4px;
      font-size: 12px;
      font-weight: 400;
      color: #161616;
      opacity: 1;
    }
  }
`


const ItemWidget: React.FC<{ msg: ShoppingItemType, widget: any, setAc: Function }> = ({msg, widget, setAc}) => {
    return (
        <ItemStyle>
            <div className="select" onClick={() => setAc()}>
                {
                    <img src={msg.isAc ? choose02 : unChoose02} alt=""/>
                }
            </div>
            <ShoppingMsgWidget {...msg} widget={widget}/>

        </ItemStyle>
    )
}


const ItemStyle = styled.div`
  background: white;
  display: flex;
  align-items: center;
  padding: 20px 15px;
  box-sizing: border-box;

  .select {
    width: 15px;
    height: 15px;
    margin-right: 10px;

    img {
      display: block;
      width: 100%;
    }
  }
`

const WapStyle = styled(InnerStyle)`
  //overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: calc(59px);
  box-sizing: border-box;

  .footer {
    position: fixed;
    bottom: 64px;
    width: 100%;
  }

  .list {
    overflow: scroll;

  }
`