import {useForm} from "react-hook-form";
import {LogInWidget} from "@/widget/base/log_in_wap";
import React, {useMemo} from "react";
import styled from "styled-components";

import loginsucessImg from "@/assets/images/loginsucess.png"
import bigbtnImg from "@/assets/images/bigbtn.png"
import {useHistory, useParams} from "react-router-dom";

export enum TisSuccess {
    niu,
    Log,
    Forgot,
    MidfPad,
    PayPad
}


export const TisSuccessPage: React.FC = () => {
    let {type} = useParams()
    const {push, goBack} = useHistory();
    console.log(type)
    const text = useMemo(() => {
        let text;
        type -= 0;
        switch (type) {
            case TisSuccess.Log:
                text = "注册成功"
                break
            case TisSuccess.Forgot:
                text = "找回密码成功"
                break
            case TisSuccess.MidfPad:
                text = "修改密码成功"
                break
            case TisSuccess.PayPad:
                text = "修改支付密码成功"
                break
        }
        return text;
    }, [type])
    return (<LogInWidget title={text} isShowBack={false}>
        <TisSuccessStyle>

            <img src={loginsucessImg} alt=""/>
            <p className={'text'}>{text}！</p>
            <div className="buttons">
                {
                    (type == TisSuccess.Forgot || type == TisSuccess.MidfPad || type == TisSuccess.Log) ?
                        <div className={'btn'} onClick={() => {
                            push("/log_in")
                        }}>立即登录
                        </div> : <div className={'btn'} onClick={() => {
                            goBack()
                        }}>返回</div>

                }

            </div>
        </TisSuccessStyle>
    </LogInWidget>);
}


const TisSuccessStyle = styled.div`

  padding: 40px 30px;
  box-sizing: border-box;
  width: 94%;
  margin: auto;
  background: rgba(255, 255, 255, 1);
  opacity: 1;
  border-radius: 15px;

  img {
    display: block;
    margin: 0 auto;
    width: 120px;
    height: 120px;
    padding-top: 80px;
    //box-sizing: border-box;
  }

  .text {
    text-align: center;
    font-size: 16px;
    margin-top: 20px;
  }

  .buttons {
    margin-top: 50px;

    > div {
      background: url("${bigbtnImg}") 100% 100%;
      background-size: 100% 100%;
      width: 290px;
      height: 49px;
      margin: 0 auto 0 auto;
      display: flex;
      justify-content: center;
      align-content: start;
      font-size: 16px;
      font-weight: 500;
      line-height: 49px;
      color: #FFFFFF;

      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
`