import styled from "styled-components";
import {useForm} from "react-hook-form";
import * as React from "react";
import {InputWidget} from "@/widget/form/input";
import {SpanWidget} from "@/widget/base/span";
import {useHistory} from "react-router-dom";


import bgImg from "@/assets/images/loginbgimage.png"
import titleImg from "@/assets/images/tongrenzhijia.png"
import loginicon01Img from "@/assets/images/loginicon01.png"
import loginmimaImg from "@/assets/images/loginmima.png"
import bigbtnImg from "@/assets/images/bigbtn.png"
import {useAuth} from "@/context/auth";
import {useCallback, useEffect, useState} from "react";
import {TestIntpu} from "@/test";
import {useAsync} from "@/utils/hooks";
import {HttpServer, SendCodeType} from "@/api/http_server";
import {CircularProgress} from "@mui/material";
import {LoadingBtn} from "@/widget/base/loading_btn";
import {VerifyCode} from "@/widget/base/sms_btn";

export const Login = () => {
    const {push} = useHistory()
    const [logErr, setLogErr] = useState(false)

    const {
        register,
        errors,
        handleSubmit,
        setValue,
        getValues
    } = useForm();

    const {
        isLoading,
        run,
        setData,
    } = useAsync()

    const {login: authLogin} = useAuth();

    const login = useCallback(
        async (form: any) => {

            try {
                await run(authLogin(form))
            } catch (e) {
                setLogErr(true)
            }

        },
        [setData],
    )

    return (
        <LogWapStyle>

            <form>
                <img className={'title'} src={titleImg} alt=""/>

                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请输入手机号码',
                             })}

                             img={loginicon01Img}
                             name={"username"}
                             placeholder={'请输入手机号码'}
                             onChange={setValue}

                />
                <SpanWidget size={15}/>
                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请输入密码',
                             })}
                             type={"password"}
                             name={"password"}
                             img={loginmimaImg}
                             placeholder={'请输入密码'}
                             setValue={setValue}
                />
                {
                    logErr &&<>
                        <SpanWidget size={15}/>
                        <InputWidget errors={errors}
                                     inputRef={register({
                                         required: '请输入验证码',
                                     })}
                                     img={loginicon01Img}
                                     placeholder={'验证码'}
                                     name={"smsCode"}
                                     setValue={setValue}
                        >
                            <VerifyCode onClick={async (cb) => {

                                let mobild = getValues('username')
                                if (!mobild) {
                                    setError('uMobile', {type: 'custom', message: '请输入手机号码'});
                                } else {
                                    cb()
                                    await HttpServer.SendCode(SendCodeType.Login, mobild)
                                }
                            }}/>
                        </InputWidget>
                    </>
                }
                <p className={'forgot'} onClick={() => push('/forgot_pad')}>忘记密码?</p>

                {
                    <div className="buttons">


                        <div className={'btn'} onClick={handleSubmit(login)}>登录
                            <LoadingBtn isShow={isLoading}/>

                        </div>
                        <div className={'btn'} onClick={() => push("log_up")}>注册</div>
                    </div>
                }

            </form>

        </LogWapStyle>
    )
}


const LogWapStyle = styled.div`
  background: url("${bgImg}") 100% 100%;
  background-size: cover;
  height: 100vh;
  padding: 0 10%;

  .title {
    display: block;
    padding: 120px 0 45px 0;
    width: 285px;
    margin: 0 auto;
    height: 75px;
  }

  .forgot {
    text-align: end;
    font-size: 11px;
    font-weight: 400;
    line-height: 15px;
    color: #FFFFFF;
    margin-bottom: 40px;
  }

  .buttons {
    > div {
      background: url("${bigbtnImg}") 100% 100%;
      background-size: 100% 100%;
      width: 290px;
      height: 49px;
      margin: 0 auto;
      margin-bottom: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      font-weight: 500;

      line-height: 49px;
      color: #FFFFFF;
      position: relative;


      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
`