export function LoopFunction({childer}) {
    const [styles, set, stop] = useSpring(() => ({
        from: {x: -40, y: 0, opacity: 1},
    }))


    return (
        <animated.div
            ref={(dom: any) => {
                if (!dom) return;
                setTimeout(() => {
                    let y = PositionConfig.y - dom.offsetTop;
                    let x = PositionConfig.x - dom.offsetLeft;
                    set({opacity: 1, y, x})
                }, 100)

            }}
            style={{
                width: 20,
                height: 20,
                borderRadius: 16,
                padding: 5,
                zIndex: 999,
                position: "fixed",
                ...styles,
            }}
        >{childer}</animated.div>
    )
}


