import React, {useEffect, useState} from "react";
import {PageWapWidget} from "@/widget/base/page_wap";
import styled from "styled-components";
import {HashRouter as Router, Redirect, Route, Switch, useHistory, useParams} from "react-router-dom";
import {ClassInnerPage} from "@/view/classification/class_inner";
import {Classification, Table} from "@/config";


export const ClassificationPage: React.FC = () => {


    return (
        <div>
            <Switch>
                <Route path={`/index/${Table.Classification}/:classification`}>
                    <PageWapWidget isShowBack={false} title={"分类"}>
                        <WapStyle>
                            <TabWidget/>
                            <div className={"inner"}>
                                <ClassInnerPage/>
                            </div>
                        </WapStyle>
                    </PageWapWidget>
                </Route>
                <Redirect to={`/index/${Table.Classification}/` + Classification.Brand}/>
            </Switch>
        </div>
    );
}

const TabWidget: React.FC = () => {
    const [classIf, setCalssIF] = useState(Classification.Brand)

    const {push} = useHistory();
    const {classification} = useParams();

    useEffect(() => {
        setCalssIF(classification)
    }, [])

    return (<div className="tab">
        <p onClick={() => {
            setCalssIF(Classification.Brand)
            push("/index/2/" + Classification.Brand)
        }}
           className={classIf == Classification.Brand ? "ac" : ""}>福利区</p>
        <p onClick={() => {
            setCalssIF(Classification.Welfare)
            push("/index/2/" + Classification.Welfare)
        }}
           className={classIf == Classification.Welfare ? "ac" : ""}>积分区</p>
    </div>)
}

const WapStyle = styled.div`
  display: flex;
  position: relative;
height: 100%;
  .tab {
    width: 80px;

    p {
      text-align: center;
      padding: 15px 0;
      box-sizing: border-box;
      opacity: 1;
      color: #8C8C8C;

      &.ac {
        background: #FFFFFF;
        color: #E8B45F;
      }
    }
  }

  .inner {
    flex: 1;
  }
`