type ItemType = {
    title: string,
    end?: any,
    path?: string
}
const ItemWidget: React.FC<ItemType> = ({title, path, end}) => {
    const {push} = useHistory();
    return (
        <ItemStyle onClick={() => {
            path && push(path)
        }}>
            <p>{title}</p>
            <div>
                {
                    end && end
                }
                {
                    path && <img src={arrowImg} alt=""/>
                }
            </div>
        </ItemStyle>
    );
}

