import React, {useMemo} from "react";
import styled from "styled-components";
import {useParams} from "react-router-dom";
import {OrderStart, OrderStatusText} from "@/view/me/widget/order_widget";
import {SpanWidget} from "@/widget/base/span";

export const OrderStatusWidget: React.FC<{orderStatus:OrderStart}> = ({orderStatus}) => {

    const statusText = useMemo(() => {
        return OrderStatusText(orderStatus);
    }, [
        orderStatus
    ])
    return (
        <OrderStatusStyle>
            <h2>{statusText}</h2>
            <SpanWidget size={9} />
            <h3>05:30订单将自动取消</h3>
        </OrderStatusStyle>
    );
}


const OrderStatusStyle = styled.div`
  padding: 20px;
  box-sizing: border-box;
  text-align: center;

  h2 {
    font-size: 16px;
    font-weight: 500;
    color: #161616;
  }
  
  h3{
    font-size: 13px;
    font-weight: 400;
    color: #494949;
  }
`