import React from 'react';
import './App.css';


import {AuthProvider, useAuth} from "./context/auth";
import {AuthWidget} from "./view/authenticated";
import {UnAuthWidget} from "./view/unauthenticated";
import {Toaster} from "react-hot-toast";
import {HashRouter as Router} from "react-router-dom";

function App() {
    const {user} = useAuth()
    return (
        <div className="App">
                {user ? <AuthWidget/> : <UnAuthWidget/>}
                <Toaster />

        </div>
    );
}



export default App;
