import React, {useCallback, useMemo, useState} from "react";
import styled from "styled-components";

import paixu3Img from "@/assets/images/home/paixu3.png"
import paixu1Img from "@/assets/images/home/paixu1.png"
import paixu2Img from "@/assets/images/home/paixu2.png"

const SortData = {
    count: 0,
    price: -1,
    addTime: -1
}
type SortType = {
    count: number,
    price: number,
    addTime: number
}
export const SortBar: React.FC = () => {
    const [value, setValue] = useState(SortData)

    const setSort = useCallback((key: string) => {
        let forward_value = value[key];
        let back_value;
        switch (forward_value) {
            case -1:
                back_value = 0
                break;
            case 0:
                back_value = 1
                break;
            case 1:
                back_value = -1
                break;
        }
        setValue({...value, [key]: back_value})
    }, [value])
    return (
        <SortBarStyle className={"sortBar"}>
            <ItemWidget click={() => setSort('count')} text={"销量"} value={value.count}/>
            <ItemWidget click={() => setSort('price')} text={"价格"} value={value.price}/>
            <ItemWidget click={() => setSort('addTime')} text={"新品"} value={value.addTime}/>

        </SortBarStyle>
    );
}

const ItemWidget: React.FC<{ text: string, value?: number, click: Function }> = ({text, value, click}) => {
    const src = useMemo(() => {
        let _src;
        switch (value) {
            case -1:
                _src = paixu3Img
                break;
            case 0:
                _src = paixu1Img
                break;
            case 1:
                _src = paixu2Img
                break;
        }
        return _src
    }, [value])
    return (
        <p onClick={() => click()} className={value >= 0 ? 'ac' : ''}>
            {text}
            <img src={src} alt=""/>
        </p>
    )
}

const SortBarStyle = styled.div`

  display: flex;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 11px 45px;
  box-sizing: border-box;

  p {
    font-size: 15px;
    font-weight: 400;
    color: #494949;
    display: flex;
    display: flex;
    align-items: center;

    img {
      display: block;
      width: 12px;
      height: 12px;
      margin-left: 5px;
    }

    &.ac {
      color: #E8B45F;
    }
  }
`