import styled from "styled-components";
import React, {useCallback, useState} from "react";
import {Classification} from "@/config";
import {useHistory, useParams} from "react-router-dom";

export const OrderWidget: React.FC = () => {

    const {classification} = useParams();


    const [classIf, setCalssIF] = useState(classification ?? Classification.Brand)


    const toPage = useCallback((type: Classification) => {
        setCalssIF(type)
    }, [classIf])

    return (<TabStyle>
        <p onClick={() => {
            toPage(Classification.Brand)
        }}
           className={classIf == Classification.Brand ? "ac" : ""}>现金</p>
        <p onClick={() => {
            toPage(Classification.Welfare)
        }}
           className={classIf == Classification.Welfare ? "ac" : ""}>积分</p>
    </TabStyle>)
}


const TabStyle = styled.div`
  margin: 0 auto;
  display: flex;
  background: rgba(255, 223, 172, 0.39);
  border-radius: 9px;

  p {
    font-size: 14px;
    font-weight: 400;
    color: #E09F35;
    opacity: 1;
    padding: 3px 0;
    width: 100px;
    box-sizing: border-box;

    &.ac {
      background: #FFFFFF;
      color: #E8B45F;
      border-radius: 9px;

    }
  }
`