//verifyCode 子组件

import React, {useEffect, useState} from "react";
import styled from "styled-components";

let timer = null

export const VerifyCode: React.FC<{ onClick }> = ({onClick}) => {
    const [time, setTime] = useState(0)

    useEffect(() => {
        timer && clearInterval(timer);
        return () => timer && clearInterval(timer);
    }, []);

    useEffect(() => {
        if (time === 60) timer = setInterval(() => setTime(time => --time), 1000)
        else if (time === 0) clearInterval(timer)
    }, [time])

    const getCode = () => {
        // 作为组件使用
        onClick && onClick(() => {
            setTime(60)
        })
        //直接使用
        // setTime(60)
    }

    return (
        <VerifyCodeStyle onClick={getCode} className=''>
            {time ? `${time}` : '发送'}
        </VerifyCodeStyle>
    )
}


const VerifyCodeStyle = styled.div`
  width: 90px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  background: #E8B45F;
  opacity: 1;
  border-radius: 5px;

  font-size: 15px;
  font-weight: 400;
  color: #FFFFFF;
`