import React from "react";
import {Login as LoginPage} from "./log_in/log_in";
import {HashRouter as Router, Switch, Route, Redirect,} from "react-router-dom";
import {ForgotPad} from "@/view/log_in/forgot_pad";
import {LogUpPage} from "@/view/log_in/log_up";
import {TisSuccessPage} from "@/view/log_in/tis_success";

export const UnAuthWidget: React.FC = () => {
    return (<div>
        <Router>
            <Switch>
                <Route path="/log_in/">
                    <LoginPage/>
                </Route>

                <Route path="/log_up">
                    <LogUpPage/>
                </Route>

                <Route path="/forgot_pad/">
                    <ForgotPad/>
                </Route>
                <Route path="/tis_success/:type">
                    <TisSuccessPage />
                </Route>

                <Redirect to="/log_in"/>

            </Switch>
        </Router>
    </div>)
}