import React from "react";
import styled from "styled-components";
import arrowImg from "@/assets/images/me/grlistarrow.png"
import {useHistory} from "react-router-dom";

type LabelType = {
    title: string,
    img: string,
    path: string
}
export const LabelWidget: React.FC<LabelType> = ({title, img, path}) => {
    const {push} = useHistory();
    return (
        <LabelStyle onClick={() => {
            path && push(path)
        }}>
            <div className="left">
                <img src={img} alt=""/>
                <p>{title}</p>
            </div>
            <div className="right">
                <img src={arrowImg} alt=""/>
            </div>
        </LabelStyle>
    )
}


const LabelStyle = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 13.5px 15px;
  box-sizing: border-box;
  background: white;

  .left {
    display: flex;

    img {
      width: 22px;
      height: 22px;
      display: block;
      margin-right: 10px;
    }

    p {
      font-size: 15px;
      font-weight: 400;
      color: #080808;
    }
  }

  .right {
    width: 16px;
    height: 16px;

    img {
      display: block;
      width: 100%;
    }
  }
`