import React, {useCallback} from "react";
import {useForm} from "react-hook-form";
import {LogInWidget} from "@/widget/base/log_in_wap";
import {InputWidget} from "@/widget/form/input";
import loginyaoqingma from "@/assets/images/loginyaoqingma.png";
import {SpanWidget} from "@/widget/base/span";
import loginming from "@/assets/images/loginming.png";
import loginicon01Img from "@/assets/images/loginicon01.png";
import loginyanzhengma from "@/assets/images/loginyanzhengma.png";
import loginmimaImg from "@/assets/images/loginmima.png";
import select02 from "@/assets/images/select02.png";
import styled from "styled-components";
import bigbtnImg from "@/assets/images/bigbtn.png";
import {useHistory} from "react-router-dom";
import {LoadingBtn} from "@/widget/base/loading_btn";
import {useAsync} from "@/utils/hooks";
import {useAuth} from "@/context/auth";
import {VerifyCode} from "@/widget/base/sms_btn";
import {HttpServer, SendCodeType} from "@/api/http_server";

export const LogUpPage: React.FC = () => {
    const {
        errors,
        register,
        handleSubmit,
        setValue,
        getValues,
        trigger
    } = useForm();
    const {push} = useHistory();

    const {
        isLoading,
        run,
        setData,
    } = useAsync()

    const {register: authRegister} = useAuth();


    const LogUp = useCallback(
        async (form: any) => {

           return  await run(authRegister(form))

        },
        [setData],
    )

    return (<LogInWidget title={"注册"}>
        <LogUpStyle>
            <form>
                <InputWidget errors={errors}
                             inputRef={register({required: '请输入邀请码',})}
                             img={loginyaoqingma}
                             placeholder={'请输入邀请码'}
                             name={"spNumber"}
                             setValue={setValue}
                />
                <SpanWidget size={10}/>
                <InputWidget errors={errors}
                             inputRef={register({required: '请输入用户名',})}
                             img={loginming}
                             placeholder={'请输入用户名'}
                             name={"nickName"}
                             setValue={setValue}
                />
                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({required: '请输入手机号码',})}
                             img={loginicon01Img}
                             placeholder={'请输入手机号码'}
                             name={"uMobile"}
                             setValue={setValue}
                />
                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({required: '请输入验证码',})}
                             img={loginyanzhengma}
                             label={'amount'}
                             placeholder={'请输入验证码'}
                             name={"smsCode"}
                             setValue={setValue}
                >
                    <VerifyCode onClick={async (cb) => {
                        let a = await trigger()


                        if (a) {
                            let mobild = getValues('uMobile')
                            cb()
                            await HttpServer.SendCode(SendCodeType.LogUp, mobild)
                        } else {
                        }
                    }}/>
                </InputWidget>


                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请输入登录密码',
                             })}
                             img={loginmimaImg}
                             placeholder={'请输入登录密码'}
                             type={"password"}
                             name={"loginPassword"}
                             setValue={setValue}
                />
            </form>

            <SpanWidget size={10}/>

            <p className={'terms'}><img src={select02} alt=""/>注册即表示您同意 <span>《隐私服务条款》及《服务协议》</span></p>

            <div className="buttons">
                {/*<div className={'btn'} onClick={() => {*/}
                {/*    push({*/}
                {/*        pathname: "tis_success/1",*/}
                {/*    })*/}
                {/*}}>注册*/}
                {/*</div>*/}

                <div className={'btn'} onClick={handleSubmit(LogUp)}>注册
                    <LoadingBtn isShow={isLoading}/>
                </div>
            </div>
        </LogUpStyle>
    </LogInWidget>);

}


const LogUpStyle = styled.div`

  padding: 40px 30px;
  box-sizing: border-box;
  width: 94%;
  margin: auto;
  background: rgba(255, 255, 255, 1);
  opacity: 1;
  border-radius: 15px;

  .sms {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .terms {
    font-size: 11px;
    display: flex;
    align-items: center;

    span {
      color: #E8B45F;
    }

    img {
      width: 18px;
      height: 18px;
      display: block;
    }
  }

  .buttons {
    margin-top: 60px;

    > div {
      background: url("${bigbtnImg}") 100% 100%;
      background-size: 100% 100%;
      position: relative;
      width: 290px;
      height: 49px;
      margin: 0 auto 10px auto;
      display: flex;
      justify-content: center;
      align-content: start;
      font-size: 16px;
      font-weight: 500;
      line-height: 49px;
      color: #FFFFFF;

      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
`