import React, {useCallback, useMemo} from "react";
import syicon01 from "@/assets/images/home/syicon01.png";
import styled from "styled-components";
import {Classification, ShopItemType} from "@/config";


export interface ShoppingItemType extends ShopItemType {
    widget?: any,
    quantity?: number,
    isAc?: boolean
}

export const ShoppingMsgWidget: React.FC<ShoppingItemType> = (props) => {

    const TextFn = useCallback((type: Classification) => {
        if (type == Classification.Welfare && props.shopType == Classification.Welfare) {
            return "积分"
        }
        if (type == Classification.Brand && props.shopType == Classification.Brand) {
            return "¥"
        }
    }, [props])
    return (<ShoppingMsgStyle>
        <div className="img">
            <img src={props.img} alt=""/>
        </div>
        <div className="msg">
            <h3>{props.title}</h3>
            <h4><span>{TextFn(Classification.Brand)} {props.price}</span>{TextFn(Classification.Welfare)}</h4>
            <h5>{TextFn(Classification.Brand)}{props.oldPrice}{TextFn(Classification.Welfare)}</h5>
            {props.widget ? props.widget : <h6>数量：x{props.quantity}</h6>}
        </div>
    </ShoppingMsgStyle>)
}

const ShoppingMsgStyle = styled.div`
  display: flex;
  flex: 1;

  .img {
    width: 91px;
    height: 91px;
    padding: 11px;
    box-sizing: border-box;
    background: rgba(243, 239, 235, 0.39);
    border-radius: 10px;
    margin-right: 10px;

    img {
      display: block;
      width: 100%;
    }
  }

  .msg {
    flex: 1;

    h3 {
      font-size: 15px;
      font-weight: 500;
      color: #080808;

      overflow: hidden;

      text-overflow: ellipsis;

      display: -webkit-box;

      -webkit-line-clamp: 2;

      overflow: hidden;

      /*! autoprefixer: off */

      -webkit-box-orient: vertical;
    }

    h4 {
      font-size: 12px;
      font-weight: 400;
      color: #494949;

      span {
        font-size: 15px;
        font-weight: 500;
        color: #E8B45F;
        opacity: 1;
      }
    }

    h5 {
      font-size: 11px;
      font-weight: 400;
      color: #A2A2A2;
      text-decoration: line-through;
    }

    h6 {
      font-size: 11px;
      font-weight: 400;
      color: #A2A2A2;
    }
  }
`