import React, {useMemo} from "react";
import {HashRouter as Router, Redirect, Route, Switch, useHistory, useParams} from "react-router-dom";
import styled from "styled-components";
import {HomePage} from "@/view/home/home";
import {ClassificationPage} from "@/view/classification/classification";
import {PositionConfig, Table} from "@/config";
import {CartPage} from "@/view/cart/cart";
import {MeWidget} from "@/view/me/me";

const reqSvgs = require.context("../assets/images/index/", true, /\.png$/);


export const IndexPage: React.FC = () => {
    const {index} = useParams();


    return (
        <IndexStyle>
            <div className="body">
                <Switch >
                    <Route path={"/index/" + Table.Home}>
                        <HomePage/>
                    </Route>
                    <Route path={"/index/" + Table.Classification}>
                        <ClassificationPage/>
                    </Route>
                    <Route path={"/index/" + Table.Cart}>
                        <CartPage/>
                    </Route>
                    <Route path={"/index/" + Table.Me}>
                        <MeWidget/>
                    </Route>
                    <Redirect to="/index/1"/>

                </Switch>
            </div>

            <div className="index_bottom">
                <NavWidget iid={1} index={index} text={"首页"}/>
                <NavWidget iid={2} index={index} text={"分类"}/>
                <img className={'logo'} src={reqSvgs(`./tabiconbig.png`)} alt=""/>
                <div ref={(dom: any) => {
                    if (dom) {
                        PositionConfig.x = dom.offsetLeft;
                        PositionConfig.y = dom.offsetTop;
                    }
                }}>
                    <NavWidget iid={3} index={index} text={"购物车"}/>
                </div>
                <NavWidget iid={4} index={index} text={"我的"}/>
            </div>
        </IndexStyle>
    );
}


const IndexStyle = styled.div`
  height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  .body {
    flex: 1;
    overflow-y: scroll;
    > div{
      height: 100%;
    }
    //overflow-x: hidden;
  }

  .index_bottom {
    height: 64px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
    opacity: 1;
    display: flex;
    z-index: 1000;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;

    .logo {
      display: block;
      width: 56px;
      height: 56px;
    }
  }
`


const NavWidget: React.FC<{ text: string, iid: number, index: number }> = ({text, iid, index}) => {
    const {push} = useHistory()
    const ac = useMemo(() => {
        return index == iid;
    }, [index])
    return (
        <NavStyle onClick={() => {
            push("/index/" + iid)
        }}>
            <img src={reqSvgs(`./${ac ? "tabpre" : "tabnor"}${iid}.png`)} alt=""/>
            <p>{text}</p>
        </NavStyle>
    );
}

const NavStyle = styled.div`
  text-align: center;

  img {
    display: block;
    width: 24px;
    height: 24px;
    margin-bottom: 5px;
  }

  p {
    font-size: 10px;
    color: #8B8B8B;
  }

`
