import {CircularProgress} from "@mui/material";
import * as React from "react";
import styled from "styled-components";

export const LoadingBtn:React.FC<{isShow:boolean}> = ({isShow}) => {
    if(!isShow) return <></>
    return (
      <div>
          <LoadingStyle onClick={(event) => event.stopPropagation()} className={'loading'}><CircularProgress size={20}/></LoadingStyle>
      </div>
    )
}


const LoadingStyle = styled.div`
    background: rgba(0, 0, 0, 0);
    position: absolute;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
`