import styled from "styled-components";
import React, {useMemo} from "react";

import grlistarrowImg from "@/assets/images/home/grlistarrow.png"


import {SpanWidget} from "@/widget/base/span";
import {useHistory} from "react-router-dom";
import {Classification, ShopItemType} from "@/config";
import {MHomeData, MokelistData} from "@/moke_data";

type ZoneType = {
    title: string,
    img: string,
    type: number
}
export const ZoneWidget: React.FC<ZoneType> = ({title, img, type}) => {
    const {push} = useHistory();


    const middleIcon = useMemo(() => {
        if (type == Classification.Brand)
            return MHomeData.homeData.shopProductsInfo.brandArea;
        else
            return MHomeData.homeData.shopProductsInfo.welfareArea;
    }, [type]);
    const System_Images_Url = MHomeData.publicHomeData.webSiteInfo.System_Images_Url

    return (
        <ZoneStyle>
            <div className="top">
                <div className={'left'}>
                    <img src={img} alt=""/>
                    <p>{title}</p>
                </div>
                <div className={'right'} onClick={() => {
                    push({
                        pathname: "/home/" + type
                    })
                }}>
                    <p>更多</p>
                    <img src={grlistarrowImg} alt=""/>
                </div>
            </div>
            <SpanWidget size={15}/>
            <div className="rows">

                <SpanWidget size={15}/>

                {middleIcon.map((ev, i) => {
                    let data: ShopItemType = {

                        img: System_Images_Url + ev.shop_Img_Show,
                        oldPrice: ev.shop_Price,
                        price: ev.shop_Price_1,
                        shopType: ev.shop_Order,
                        title: ev.shop_Name,

                    }
                    return (<div key={i + title} style={{marginRight: "15px"}}>
                        <ItemWidget  {...data}/>
                    </div>);
                })}
            </div>
        </ZoneStyle>
    )
}

const ZoneStyle = styled.div`
  padding: 15px 0;
  box-sizing: border-box;
  background: white;

  .top {
    display: flex;
    padding: 0 15px;

    justify-content: space-between;
    align-items: center;

    > div {
      display: flex;
      align-items: center;

      img {
        display: block;
        width: 22px;
        height: 22px;
      }

      p {
        font-size: 15px;
        font-weight: 400;
      }
    }

    .left {
      img {
        margin-right: 4px;
      }
    }

    .right {
      p {
        color: #8C8C8C;
      }
    }
  }


  .rows {
    white-space: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-width: none;

    > div {
      white-space: nowrap;
      display: inline-block;

    }
  }
`;

const ItemWidget: React.FC<ShopItemType> = (props) => {
    return (
        <ItemStyle>
            <div className="img">
                <img src={props.img} alt=""/>
            </div>
            <h4>{props.title}</h4>
            <h5>￥{props.price}</h5>
            <h6>￥{props.oldPrice}</h6>
        </ItemStyle>
    );
}

const ItemStyle = styled.div`
  width: 120px;

  .img {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 120px;
    border-radius: 10px;
    background: rgba(243, 239, 235, 0.39);

    img {
      display: block;
      width: 85px;
      height: 85px;
    }
  }

  h4 {
    font-size: 14px;
    font-weight: 400;
    color: #080808;
    margin-top: 9px;
    white-space: normal;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;


    display: -webkit-box;

    -webkit-line-clamp: 2;

    /*! autoprefixer: off */

    -webkit-box-orient: vertical;
  }

  h5 {
    font-size: 15px;
    font-weight: 500;
    color: #E8B45F;
  }

  h6 {
    font-size: 11px;
    font-weight: 400;
    color: #A2A2A2;
    text-decoration: line-through;
  }
`