import React, {useCallback} from "react";
import styled from "styled-components";
import {LogInWidget} from "@/widget/base/log_in_wap";
import {useForm} from "react-hook-form";
import {InputWidget} from "@/widget/form/input";
import {SpanWidget} from "@/widget/base/span";

import loginmimaImg from "@/assets/images/loginmima.png";
import loginicon01Img from "@/assets/images/loginicon01.png";
import loginming from "@/assets/images/loginming.png";
import loginyanzhengma from "@/assets/images/loginyanzhengma.png";
import bigbtnImg from "@/assets/images/bigbtn.png"
import {useHistory, useParams} from "react-router-dom";
import {TisSuccess} from "@/view/log_in/tis_success";
import {VerifyCode} from "@/widget/base/sms_btn";
import {HttpServer, SendCodeType} from "@/api/http_server";
import {LoadingBtn} from "@/widget/base/loading_btn";
import {useAsync} from "@/utils/hooks";


export const ForgotPad: React.FC = () => {

    const {
        isLoading,
        run,
        setData,
    } = useAsync()

    const {isLog} = useParams()
    const {register, errors, handleSubmit, setValue, setError, getValues} = useForm();
    const {push, goBack} = useHistory();
    let title = isLog ? "修改密码" : "找回密码"

    const forgotFn = useCallback(
        async (form: any) => {
            const {uMobile, smsCode, loginPassword, login2Password} = form;
            if (login2Password !== loginPassword) {
                setError('login2Password', {type: 'custom', message: '两次密码不一致'});
                return
            }
            try {

                let data = await run(HttpServer.UserFindPwd(uMobile, smsCode, loginPassword));
                if (isLog) {

                    push(`/me/tis_success/${TisSuccess.MidfPad}`);
                } else {
                    push(`/tis_success/${TisSuccess.MidfPad}`);
                }
            } catch (e) {
                console.log(e)
            }

        },
        [setData],
    )
    return (<LogInWidget title={title}>
        <ForgotPadStyle>
            <form>
                <InputWidget errors={errors}
                             inputRef={register({required: '请输入手机号码'})}
                             img={loginming}
                             placeholder={'请输入手机号码'}
                             name={"uMobile"}
                             setValue={setValue}
                />
                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请输入验证码',
                             })}
                             img={loginicon01Img}
                             placeholder={'验证码'}
                             name={"smsCode"}
                             setValue={setValue}
                >
                    <VerifyCode onClick={async (cb) => {

                        let mobild = getValues('uMobile')
                        if (!mobild) {
                            setError('uMobile', {type: 'custom', message: '请输入手机号码'});
                        } else {
                            cb()
                            await HttpServer.SendCode(SendCodeType.MdifPad, mobild)
                        }
                    }}/>
                </InputWidget>
                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请输入新密码',
                             })}
                             type={"password"}
                             img={loginyanzhengma}
                             label={'amount'}
                             placeholder={'请输入新密码'}
                             name={"loginPassword"}
                             setValue={setValue}
                />
                <SpanWidget size={10}/>

                <InputWidget errors={errors}
                             inputRef={register({
                                 required: '请再次输入新密码',
                             })}
                             type={"password"}
                             img={loginmimaImg}
                             placeholder={'请再次输入新密码'}
                             name={"login2Password"}
                             setValue={setValue}
                />


                <div className="buttons">
                    <div className={'btn'} onClick={handleSubmit(forgotFn)}>完成
                        <LoadingBtn isShow={isLoading}/>
                    </div>
                </div>
            </form>

        </ForgotPadStyle>
    </LogInWidget>);
}


const ForgotPadStyle = styled.div`

  padding: 40px 30px;
  box-sizing: border-box;
  width: 94%;
  margin: auto;
  background: rgba(255, 255, 255, 1);
  opacity: 1;
  border-radius: 15px;


  .buttons {
    margin-top: 50px;

    > div {
      position: relative;
      background: url("${bigbtnImg}") 100% 100%;
      background-size: 100% 100%;
      width: 290px;
      height: 49px;
      margin: 0 auto 0 auto;
      display: flex;
      justify-content: center;
      align-content: start;
      font-size: 16px;
      font-weight: 500;
      line-height: 49px;
      color: #FFFFFF;

      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
`