import {Api} from "@/api/api";

export enum SendCodeType {
    niu,
    Login,
    LogErr,
    LogUp,
    MdifPad,
    PayPad,
}

export class HttpServer {
    static account: string | null;

    static Login(loginID: string, password: string, smsCode?: string): Promise<any> {
        return Api.post('/UserLogin/Login', {
            loginID,
            password,
            phoneKey: "phoneKey",
            version_Origin: "1",
            "u_Type": 1,
            "sms_Code":smsCode,

            versionInternalNumber: 1
        });
    }

    static LogUp(uMobile: string, smsCodeL: string, spNumber: string, loginPassword: string, nickName: string): Promise<any> {
        return Api.post('/UserLogin/User_Application', {
            "uMobile": uMobile,
            "smsCode": smsCodeL,
            "u_Type": 1,
            "spNumber": spNumber,
            "loginPassword": loginPassword,
            nickName
        });
    }

    // 找回密码
    static UserFindPwd(uMobile: string, smsCode: string, loginPassword: string): Promise<any> {
        return Api.post('/UserLogin/User_FindPwd', {
            "loginPassword": loginPassword,
            "uMobile": uMobile,
            "uType": 0,
            "smsCode": smsCode
        });
    }

    // 首页数据
    static UserHomeData(uMobile: string, smsCode: string, loginPassword: string): Promise<any> {
        return Api.post('/Home/User_HomeData', {
            "loginPassword": loginPassword,
            "uMobile": uMobile,
            "uType": 0,
            "smsCode": smsCode
        });
    }

    // 发送验证码
    static SendCode(type: SendCodeType, strConut: string) {
        return Api.post('/SendCode', {strConut, "u_Type": type})
    }


}
