import {QueryCache} from 'react-query'
import React, {createContext, ReactNode, SuspenseProps, useCallback, useState} from "react";
import {useAsync} from "../utils/hooks";
import {client} from "../utils/api-client";
import {HttpServer} from "@/api/http_server";
import toast, {Toaster} from "react-hot-toast";
import {TisSuccess} from "@/view/log_in/tis_success";
import {useHistory} from "react-router-dom";

const AuthContext = createContext<AuthInterface>({
    login(ev: any) {
    },
    logout(ev: any) {
    },
    register(ev: any) {
    },
    user: {},
})


interface AuthInterface {
    user: any,
    login: Function,
    logout: Function,
    register: Function,
}


async function bootstrapAppData() {
    let user = null

    const token = localStorage.getItem("token")
    if (token) {
        user = JSON.parse(token)
    }
    return user
}


export const AuthProvider: React.FC<SuspenseProps> = ({children}) => {

    const {
        data: user,
        status,
        error,
        isLoading,
        isIdle,
        isError,
        isSuccess,
        run,
        setData,
    } = useAsync()

    React.useEffect(() => {
        const appDataPromise = bootstrapAppData()
        run(appDataPromise)
    }, [run])

    const {push, goBack} = useHistory();


    const login = React.useCallback(
        async (form: any) => {
            try {
                let data = await run(HttpServer.Login(form.username, form.password, form?.smsCode))
                setTokne(data)
            } catch (e) {
                throw error
            }

        }, [setData],
    )

    const setTokne = useCallback((token) => {
        localStorage.setItem("token", JSON.stringify(token))
    }, [])

    const register = React.useCallback(
        async (form: any) => {
            try {
                const {uMobile, smsCode, spNumber, loginPassword, nickName} = form
                let data = await run(HttpServer.LogUp(uMobile, smsCode, spNumber, loginPassword, nickName))
                push(`/tis_success/${TisSuccess.Log}`);

                // await HttpServer.Login()
                // setData({
                //     token: '---------------',
                //     name: "呜喵王之怒",
                //     id: "783752"
                // })
            } catch (e) {
                console.log(e)
            }
        },
        [setData],
    )
    const logout = React.useCallback(() => {

        setData(null)
    }, [setData])

    const value = React.useMemo(
        () => ({user, login, logout, register}),
        [login, logout, register, user],
    )

    // if (isLoading || isIdle) {
    //     return <span>等待中</span>
    // }

    // if (isError) {
    //     return <span>dasdasd</span>
    //
    //     // return <FullPageErrorFallback error={error}/>
    // }

    return <AuthContext.Provider value={{
        ...value
    }}>
        <span>{children}</span>
    </AuthContext.Provider>

}


export const useAuth = () => {
    const context = React.useContext(AuthContext)
    if (context === undefined) {
        throw new Error(`useAuth must be used within a AuthProvider`)
    }
    return context
}

function useClient() {
    const {user} = useAuth()
    const token = user?.token
    return React.useCallback(
        (endpoint: any, config: any) => client(endpoint, {...config, token}),
        [token],
    )
}