import React, {useMemo} from "react";
import styled from "styled-components";
import {ShoppingItemType, ShoppingMsgWidget} from "@/view/cart/widget/shopping_msg";
import {useHistory} from "react-router-dom";
import {SpanWidget} from "@/widget/base/span";


export enum OrderStart {
    niu,
    UnPay,
    ToBeDelivered,
    Pendingreceipt,
    Success
}

export const OrderStatusText = (status: OrderStart) => {
    let _text;
    status -= 0;
    switch (status) {
        case OrderStart.UnPay:
            _text = "待支付";
            break;
        case OrderStart.ToBeDelivered:
            _text = "待发货";
            break;
        case OrderStart.Pendingreceipt:
            _text = "订单已发货";
            break;
        case OrderStart.Success:
            _text = "订单已完成";
            break;
    }
    return _text;
}

interface OrderType {
    status: number,
    orderId: string,
    shopping: ShoppingItemType[]
}

export const OrderItemWidget: React.FC<{ msg: OrderType }> = ({msg}) => {
    const {status} = msg;

    const headerStatus = useMemo(() => {
        return OrderStatusText(status)
    }, [status])

    const {push} = useHistory();

    return (
        <ItemStyle onClick={() => {
            push(`/me/order_details/${msg.orderId}/${status}`)
        }}>
            <h1 className="title">
                订单号：{msg.orderId}
                <span>{headerStatus}</span>
            </h1>
            {
                msg.shopping.map((ev, i) => <div key={"ShoppingMsgWidget" + i}>
                        <ShoppingMsgWidget {...ev}/>
                        <SpanWidget size={10}/>
                    </div>
                )
            }
            {
                status == OrderStart.UnPay && <>
                    <p className={'pay_price'}>需支付￥200.00</p>
                    <div className="buttons">
                        <div className="btn pay">立即支付</div>
                    </div>
                </>
            }

            {
                status == OrderStart.ToBeDelivered && <>
                    <p className={'pay_price'}>实付款￥200.00</p>
                    <div className="buttons">
                        <div className="btn pay">确认收货</div>
                    </div>
                </>
            }
            {
                status == OrderStart.Pendingreceipt && <>
                    <p className={'pay_price'}>实付款￥200.00</p>
                    <div className="buttons">
                        <div className="btn">查看物流</div>
                        <div className="btn pay">确认收货</div>
                    </div>
                </>
            }

            {
                status == OrderStart.Success && <>
                    <p className={'pay_price'}>实付款￥200.00</p>
                    <div className="buttons">
                        <div className="btn pay">删除订单</div>
                    </div>
                </>
            }
        </ItemStyle>
    );
}


const ItemStyle = styled.div`
  background: white;
  align-items: center;
  padding: 20px 15px;
  box-sizing: border-box;

  .title {

    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(209, 209, 209, 0.39);
    margin-bottom: 10px;
    padding-bottom: 14px;
    line-height: 1;

    font-size: 13px;
    font-weight: 400;
    color: #494949;

    span {
      font-size: 13px;
      font-weight: 400;
      color: #494949;
    }
  }

  .msg h3 {
    -webkit-line-clamp: 1;
  }

  .pay_price {
    font-size: 16px;
    font-weight: 400;
    color: #080808;
    text-align: end;
    line-height: 1;
  }

  .buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
  }

  .btn {
    //background: rgba(255, 255, 255, 0.39);
    opacity: 1;
    border-radius: 30px;
    padding: 5.5px 17.5px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 1;
    border: 1px solid #B5B5B5;
    margin-left: 10px;

    &.pay {
      border: 1px solid #E8B45F;
      font-family: Alibaba PuHuiTi;
      font-weight: 400;
      color: #E8B45F;
    }
  }
`
