import React, {useEffect} from "react";
import {IndexPage} from "@/view/table";
import {HashRouter as Router, Redirect, Route, Switch, useHistory} from "react-router-dom";
import {BrandPage} from "@/view/home/brand";
import {SetPage} from "@/view/me/set_page";
import {ForgotPad} from "@/view/log_in/forgot_pad";
import {TisSuccessPage} from "@/view/log_in/tis_success";
import {OrderPage} from "@/view/me/order";
import {OrderDetailsPage} from "@/view/me/order_details";
import {ShareImgPage, SharePage} from "@/view/me/share";


export const AuthWidget: React.FC = () => {
    return <div>
        <Router>
            <Switch>

                <Route path="/index/:index" component={IndexPage}/>


                <Route path="/home/:type" component={BrandPage}/>


                <Route path="/me/set_up" component={SetPage}/>
                <Route path="/me/forgot_pad/:isLog" component={ForgotPad}/>
                <Route path="/me/tis_success/:type" component={TisSuccessPage}/>
                <Route path="/me/order/:classification" component={OrderPage}/>
                <Route path="/me/order_details/:orderId/:orderStatus" component={OrderDetailsPage}/>
                <Route path="/me/share" component={SharePage}/>
                <Route path="/me/share_img/:url" component={ShareImgPage}/>

                <Route path="/">
                    <ToIndexWidget/>
                </Route>
            </Switch>
        </Router>
    </div>
}


const ToIndexWidget: React.FC = () => {
    const {push} = useHistory()
    useEffect(() => {
        push("/index/1")
    }, [])
    return (<div></div>)
}