import React, {useCallback, useState} from "react";
import {PageWapWidget} from "@/widget/base/page_wap";
import {OrderWidget} from "@/view/me/widget/order_tab";
import styled from "styled-components";
import {OrderItemWidget} from "@/view/me/widget/order_widget";
import {MokelistData} from "@/moke_data";
import {SpanWidget} from "@/widget/base/span";


export const OrderPage: React.FC = () => {

    let tabsValue = [
        "全部",
        "待付款",
        "待发货",
        "待收货",
        "已完成"
    ];

    const [tabValue, setTabValue] = useState(tabsValue[0]);


    return (
        <PageWapWidget title={""} widget={<OrderWidget/>}>
            <WapStyle>
                <div className="tabs">
                    {
                        tabsValue.map((ev) => {
                            return <div key={ev} onClick={() => {
                                setTabValue(ev)
                            }} className={ev === tabValue ? 'ac' : 'un'}>
                                <span>{ev}</span>
                            </div>
                        })
                    }
                </div>

                <div className="body">
                    {
                        MokelistData.map((ev, i) => {
                                return (
                                    <div key={"order" + i}>
                                        <SpanWidget size={10}/>
                                        <OrderItemWidget msg={{
                                            shopping: [
                                                ev, ev
                                            ], status: i + 1, orderId: "001----------" + i
                                        }}/>
                                    </div>
                                );
                            }
                        )
                    }
                </div>
            </WapStyle>
        </PageWapWidget>
    )
}

const WapStyle = styled.div`
  .tabs {
    display: flex;
    justify-content: space-between;
    background: white;
    position: fixed;
    width: 100%;
    height: 43px;

    div {
      flex: 1;
      //display: flex;
      //justify-content: center;
      text-align: center;

      &.ac {
        span {
          color: #E8B45F;
          border-bottom: 2px solid #E8B45F;
        }
      }

      span {
        font-size: 15px;
        font-weight: 400;
        color: #494949;
        display: inline-block;
        //margin: 0 auto;
        flex: 1;
        padding: 9px 0;
        box-sizing: border-box;
        text-align: center;
        border-bottom: 2px solid transparent;


      }
    }
  }

  .body {
    padding-top: 43px;
    box-sizing: border-box;
  }
`
