import React, {useEffect, useMemo} from "react";
import styled from "styled-components";

import choose02 from "@/assets/images/cart/choose02.png"
import unChoose02 from "@/assets/images/cart/choose02 (1).png"
import {ShoppingItemType} from "@/view/cart/widget/shopping_msg";
import {useHistory} from "react-router-dom";
import {OrderStart} from "@/view/me/widget/order_widget";

export const CartBottomWidget: React.FC<{ payData: { data: ShoppingItemType[], i: number } }> = ({payData}) => {

    const {push} = useHistory();
    const AllPrice = useMemo<{ price: number, count: number }>(() => {
        if (!payData || payData.data.length === 0) return;
        let price = 0;
        let count = 0;
        payData.data.forEach((ev) => {
            if (ev.isAc) {
                price += ev.quantity * ev.price;
                count += ev.quantity ?? 0;
            }
        });
        return {price, count};
    }, [payData.i])

    return (
        <CartBottomStyle>
            <div className="left">
                <div className="all">
                    <img src={choose02} alt=""/>
                    全选
                </div>

                <div className="prices">
                    已选 {AllPrice.count} 个 合计： <span>¥{AllPrice.price}</span>
                </div>
            </div>

            <div className="button" onClick={() => {
                push(`/me/order_details/-----1/${OrderStart.niu}`)
            }}>去结算</div>
        </CartBottomStyle>
    );
}


const CartBottomStyle = styled.div`
  height: 59px;
  background: rgba(255, 255, 255, 1);
  display: flex;
  justify-content: space-between;

  .left {
    flex: 1;
    display: flex;
    padding: 0 10px;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;

    .all {
      display: flex;
      align-items: center;

      font-size: 12.5px;
      font-weight: 400;
      color: #161616;
      opacity: 1;

      img {
        display: block;
        width: 15px;
        height: 15px;
        margin-right: 8px;
      }
    }

    .prices {
      font-size: 12px;
      font-weight: 400;
      color: #161616;

      span {
        font-size: 14px;
        font-weight: 500;
        color: #E8B45F;
      }
    }
  }

  .button {
    width: 100px;
    background: #E8B45F;

    font-size: 16px;
    font-weight: 500;
    color: #FFFFFF;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
`