import React from "react";
import {PageWapWidget} from "@/widget/base/page_wap";
import {AddressItem} from "@/view/me/widget/address_item";
import {OrderStatusWidget} from "@/view/me/widget/order_status";
import {SpanWidget} from "@/widget/base/span";
import styled from "styled-components";
import {ShoppingMsgWidget} from "@/view/cart/widget/shopping_msg";
import {MokelistData} from "@/moke_data";
import {OrderStart} from "@/view/me/widget/order_widget";
import {useParams} from "react-router-dom";
import {BigBtnWidget} from "@/view/me/widget/button";

export const OrderDetailsPage: React.FC = () => {
    const {orderStatus} = useParams();

    return (
        <PageWapWidget title={"订单详情"}>
            {
                orderStatus != OrderStart.niu && <OrderStatusWidget orderStatus={orderStatus}/>
            }
            <AddressItem/>
            <SpanWidget size={10}/>
            <OrderMsg/>

            <BigBtnWidget text={"提交订单"} />
        </PageWapWidget>
    );
}

const OrderMsg: React.FC = () => {
    const {orderStatus} = useParams();

    return (
        <OrderStyle>
            <h3 className={"msg_title"}>购买产品</h3>
            {[
                MokelistData[orderStatus - 1],
            ].map((ev, i) => {
                    return (
                        <div key={"order_details" + i}>
                            <ShoppingMsgWidget {...ev}/>
                            <SpanWidget size={10}/>
                        </div>
                    );
                }
            )
            }
            <div className="tab">
                <p>商品总价</p>
                <span>500</span>
            </div>
            <div className="tab">
                <p>运费</p>
                <span>积分</span>
            </div>
            <div className="tab">
                <p>总计</p>
                <span className={'ac'}>500<span>包邮</span></span>
            </div>

            {
                orderStatus != OrderStart.niu && <>
                    <div className={"link"}/>
                    <div className="tab">
                        <p>订单编号</p>
                        <span>908852928592985</span>
                    </div>
                    <div className="tab">
                        <p>创建时间</p>
                        <span>2022-03-20</span>
                    </div>
                    <div className="tab">
                        <p>付款时间</p>
                        <span>2022-03-03</span>
                    </div>
                    <div className="tab">
                        <p>发货时间</p>
                        <span>待发货</span>
                    </div>
                </>
            }
        </OrderStyle>
    );
}


const OrderStyle = styled.div`
  padding: 20px 15px;
  box-sizing: border-box;
  background: white;

  .msg_title {
    font-size: 15px;
    font-weight: 400;
    color: #161616;
    margin-bottom: 20px;
  }

  > div {
    .msg {
      h3 {
        -webkit-line-clamp: 1;
      }
    }
  }


  .link {
    width: 100%;
    height: 1px;
    background: rgba(209, 209, 209, 0.39);
    margin-top: 15px;
  }

  .tab {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;

    p {
      font-size: 15px;
      font-weight: 400;
      color: #080808;
    }

    span {
      font-size: 15px;
      font-weight: 400;
      color: #8C8C8C;
      display: flex;
      align-items: center;

      &.ac {
        font-size: 15px;
        font-weight: 500;
        color: #E8B45F;

        span {
          font-size: 12px;
          font-weight: 400;
          color: #E8B45F;
        }
      }
    }
  }
`