import React from "react";
import {useParams} from "react-router-dom";
import styled from "styled-components";
import {SortBar} from "@/view/home/widget/sort_bar";
import {ItemWidget as ShopWidget} from "@/widget/base/shop";
import {ShopItemType} from "@/config";
import {MokelistData} from "@/moke_data";

export const ClassInnerPage: React.FC = () => {
    const {classification} = useParams();
    let listData: ShopItemType[] = MokelistData;

    return (<WapStyle>
        <SortBar/>

        <div className="list">
            {
                listData.map((ev, i) => {
                    return <div key={i + "-="}>
                        <ItemWidget {...ev} shopType={classification}/>
                    </div>
                })
            }
        </div>

    </WapStyle>);
}
const WapStyle = styled.div`
  height: calc(100vh - 56.5px - 64px);
  background: white;
  overflow-x: hidden;
  position: absolute;
  .sortBar{
    position: relative;
    top: 0;
  }
  >.list{
    padding: 0 15px;
    overflow-x: scroll;
    box-sizing: border-box;
  }
`;


const ItemWidget: React.FC<ShopItemType> = (props) => {
    return (<ItemStyle>
        <div className="inner">
            <div className="img">
                <img src={props.img} alt=""/>
            </div>
            <div className={'right'}>
                <ShopWidget {...props} />
            </div>
        </div>
    </ItemStyle>)
}
const ItemStyle = styled.div`
  box-sizing: border-box;
  width: 100%;
  opacity: 1;
  //border-radius: 5px;
  padding: 15px 0;

  border-bottom: 1px solid #D1D1D1;

  .inner {
    box-sizing: border-box;
    background: #fff;
    display: flex;

    .img {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 91px;
      width: 91px;
      padding: 11px;
      box-sizing: border-box;
      background: rgba(243, 239, 235, 0.39);
      opacity: 1;
      border-radius: 10px;

      img {
        width: 100%;
        display: block;
      }
    }

    .right {
      padding-left: 10px;
      box-sizing: border-box;
      flex: 1;
      h4{
        margin-top: 0;
      }

      h4{
        overflow: hidden;

        text-overflow: ellipsis;

        display: -webkit-box;

        -webkit-line-clamp: 2;

        overflow:hidden;

        /*! autoprefixer: off */

        -webkit-box-orient: vertical;
      }
    }
  }
`


