import React, {LegacyRef, RefObject, useState} from "react";
import styled from "styled-components";
import ShopImg from "@/assets/images/home/tabpre3.png"
import {useParams} from "react-router-dom";
import {ShopItemType} from "@/config";
import useDynamicRefs from 'use-dynamic-refs';
import {LoopFunction} from "@/view/me/me";


export const ItemWidget: React.FC<ShopItemType> = (props) => {
    const [count, setCount] = useState(false);
    const [getRef, setRef] = useDynamicRefs();

    const btnClick = () => {
        setTimeout(() => {
            setCount(false);
        }, 500)
        setCount(true);
    };

    return (<ItemStyle>
        <h4>{props.title}</h4>
        {

            count && <LoopFunction
                key={props.oldPrice}
                onClick={() => {
                    const target = (props.oldPrice as RefObject<HTMLDivElement>).current as HTMLDivElement;
                    console.log(target.innerText);
                }}
                childer={<img style={{width:"100%",height:"100%"}} src={props.img} alt=""/>}
            />

        }
        {
            props.shopType == 1 ? <h5>￥{props.price} <span>￥{props.oldPrice}</span></h5>
                : <h5>{props.price} 积分 <span>{props.oldPrice}积分</span></h5>

        }
        <h6>月销2640 <div onClick={btnClick}>
            <img src={ShopImg} alt=""/>
        </div></h6>
    </ItemStyle>)
}


const ItemStyle = styled.div`
  h4 {
    font-size: 14px;
    font-weight: 400;
    color: #080808;
    margin-top: 9px;
    white-space: normal;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  h5 {
    font-size: 15px;
    font-weight: 500;
    color: #E8B45F;

    span {
      font-size: 11px;
      font-weight: 400;
      color: #A2A2A2;
      text-decoration: line-through;
    }
  }

  h6 {
    margin-top: 2px;
    font-size: 11px;
    color: #A2A2A2;
    opacity: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
      display: block;
      width: 22px;
      height: 22px;
    }
  }
`