import axios, {AxiosInstance} from 'axios';
import React from "react";
import toast, {Toaster} from 'react-hot-toast';

type OnPresentType = (content: React.ReactNode, key?: string) => void;


export class NetAxios {
    private axios: AxiosInstance;
    private baseURL: string;


    set setPresent(fn: OnPresentType) {
        this.onPresent = fn;
    }

    public onPresent: OnPresentType | any;

    constructor(baseURL: string) {
        this.baseURL = baseURL;
        this.axios = axios.create({
            timeout: 30000,
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            },
            transformRequest: [
                (data) => {
                    return JSON.stringify(data);
                }
            ],
            transformResponse: [
                (data) => {
                    return JSON.parse(data);
                }
            ]
        });
        this.interceptors();
    }

    /**
     * 默认拦截器
     */
    interceptors() {
        // 请求拦截器
        this.axios.interceptors.request.use(
            (config) => {
                // if (localStorage.getItem('token')) {
                if (config.data) {
                    config.data['phoneKey'] = "0";
                    config.data['versionNumber'] = "1.0.0";
                    config.data['versionOrigin'] = 0;
                }

                // }
                return Promise.resolve(config);
            },
            (error) => {
                return Promise.reject(error);
            }
        );

        // 响应拦截器
        this.axios.interceptors.response.use(
            (response) => {
                const {status, data} = response;
                const {code, result} = data;
                if (status === 200) {
                    if (data.success) {
                        return Promise.resolve(data.data);
                    } else {
                        toast.error(data.messages, {duration: 1500})
                        return Promise.reject(data);
                    }
                }
                toast.error(data.messages ?? "稍后重试", {duration: 1500})
                return Promise.reject(data);
            },
            (error) => {
                toast('Here is your toast.')


                return Promise.reject(error);
            }
        );
    }

    get(url: string, params: any, baseUrl = true) {

        return this.axios.get(`${baseUrl ? this.baseURL : ''}${url}`, {params: {...params}})
    }

    post(url: string, data: any) {
        return this.axios.post(`${this.baseURL}${url}`, data)
    }

    put(url: string, data: any) {
        return this.axios.put(url, data)
    }

    patch(url: string, data: any) {
        return this.axios.patch(url, data)
    }

    delete(url: string) {
        return this.axios.delete(url)
    }

}

export const Api = new NetAxios("http://app.trzjapp.com/api");


