import React from "react";
import styled from "styled-components";
import Slider, {Settings} from "react-slick";
import homebannerImg from "@/assets/images/home/homebanner.png"
import gonggaoImg from "@/assets/images/home/gonggao.png"
import {MHomeData} from "@/moke_data";

export const BannerWidget: React.FC = () => {
    let settings: Settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        autoplay: true,
        slidesToScroll: 1,
        arrows: false,
        centerPadding: "20px",
    };

    const banners = MHomeData.publicHomeData.appCofig.topBanner;
    const System_Images_Url = MHomeData.publicHomeData.webSiteInfo.System_Images_Url
    return (
        <BannerStyle>
            <Slider {...settings}>
                {
                    banners.map((ev) => <div key={ev + "bann"}>
                        <img className={'img'} src={System_Images_Url + ev.apP_Pic} alt=""/>
                    </div>)
                }
            </Slider>
            <div className="public">
                <img src={gonggaoImg} alt=""/>
                <span>通知：邀请好友即可立即领取500积分购物券，限时限量。</span>
            </div>
        </BannerStyle>
    );
}

const BannerStyle = styled.div`
  padding: 0 10px;

  box-sizing: border-box;

  .img {
    display: block;
    width: 100%;
    height: 165px;
    box-sizing: border-box;
    border-radius: 5px;
  }

  .slick-dots {
    bottom: 5px;

    li.slick-active button:before {
      opacity: 1;
    }

    li {
      height: 3px;
      width: 11px;

      button {
        height: 3px;
        width: 11px;

      }

      button:before {
        height: 3px;
        content: "";
        background: #fff;
        width: 11px;
        border-radius: 3px;
      }
    }
  }

  .public {
    display: flex;
    align-items: center;

    img {
      display: block;
      height: 15px;
      margin-right: 5px;
    }

    span {
      font-size: 12px;
    }
  }
`