import React from "react";
import {PageWapWidget} from "@/widget/base/page_wap";
import {SortBar} from "@/view/home/widget/sort_bar";
import styled from "styled-components";
import syicon01 from "@/assets/images/home/syicon01.png";
import {SpanWidget} from "@/widget/base/span";
import {useParams} from "react-router-dom";
import {ItemWidget as ShopWidget} from "@/widget/base/shop"
import {MokelistData} from "@/moke_data";
import {ShopItemType} from "@/config";

export const BrandPage: React.FC = () => {

    let listData: ShopItemType[] = MokelistData;
    const {type} = useParams();

    return (
        <PageWapWidget title={type == 1 ? "品牌区" : "福利区"}>
            <SortBar/>
            <WapStyle>
                <div className={'col'}>
                    {
                        listData.map((ev, i) => {
                            if (i % 2 === 0)
                                return <div key={i + "-"}>
                                    <ItemWidget {...ev} shopType={type}/>
                                </div>
                        })
                    }
                </div>
                <SpanWidget size={10}/>
                <div className={'col'}>
                    {
                        listData.map((ev, i) => {
                            if (i % 2 !== 0)
                                return <div key={i + "-="}>
                                    <ItemWidget {...ev} shopType={type}/>
                                </div>
                        })
                    }
                </div>

            </WapStyle>
        </PageWapWidget>
    )
}

const WapStyle = styled.div`
  padding: 10px;
  display: flex;

  > .col {
    flex: 1;
  }
`


const ItemWidget: React.FC<ShopItemType> = (props) => {
    return (<ItemStyle>
        <div className="inner">
            <div className="img">
                <img src={props.img} alt=""/>
            </div>
            <ShopWidget {...props} />
        </div>
    </ItemStyle>)
}
const ItemStyle = styled.div`
  padding: 5px;
  display: inline-block;
  width: 100%;
  opacity: 1;
  border-radius: 5px;

  .inner {
    box-sizing: border-box;
    background: #fff;
    width: 100%;
    border-radius: 10px;
    padding: 10px;

    .img {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 120px;


      img {
        display: block;
        width: 85px;
        height: 85px;
      }
    }
  }
`